.Art-container {
  background-color: #52414c;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .Art-content {
    max-width: 60vw;
    P {
      padding-bottom: 2.7rem;
    }
    h1 {
      padding: 4rem;
      font-size: 3.5rem;
      font-family: Norse;
    }
    h2 {
      padding: 1.5rem;
      font-size: 2.2rem;
    }
    h3 {
      padding: 1rem;
      font-size: 1.3rem;
    }
  }
}
@media (max-width: 768px) {
  .Art-container {
    .Art-content {
      max-width: 90vw;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      h1 {
        font-size: 2.5rem;
        padding: 0;
        padding-top: 3rem;
        padding-bottom: 2rem;
      }
      h2 {
        font-size: 2rem;
        padding: 0;
        padding-top: 3rem;
        padding-bottom: 2rem;
      }
      section {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }
      p {
        max-width: 70vw;
      }
    }
  }
}
